import "../styles/editor.scss";
import { useEffect } from "@wordpress/element";
import "../styles/editor.scss";
import { __ } from "@wordpress/i18n";
import { prefix } from "../../../constants";
import InspectorPanel from "./inspector";
import {
	getTypoStyle,
	getDimensionStyle,
	getResponsiveRangeVal,
} from "../../../helpers";

import {
	RichText,
	BlockControls,
	useBlockProps,
	AlignmentToolbar,
	InnerBlocks,
} from "@wordpress/block-editor";

const Edit = (props) => {
	const {
		setAttributes,
		attributes: {
			bgImage,
			bgPosition,
			bgColor,
			bgAttachment,
			bgSize,
			bgRepeat,
			padding,
			overlay,
			opacity,
			borderRadius,
			contentWidth,
			heading,
			alignment,
			headingColor,
			headingTypo,
			subDescription,
			subDescriptionColor,
			subDescriptionTypo,
			subDescriptionMargin,
			contentAlignment,
		},
	} = props;

    const blockProps = useBlockProps({
        className: `content-alignment-${contentAlignment}`,
    });

	useEffect(() => {
		setAttributes({
			block_id: `${prefix}-cta-${blockProps.id}`,
		});
	});

	const textStyle = {
		...getTypoStyle(headingTypo),
		color: headingColor,
	};

	const subDescriptionStyle = {
		...getTypoStyle(subDescriptionTypo),
		color: subDescriptionColor,
		...getDimensionStyle("margin", subDescriptionMargin),
	};

	const wrapperStyle = {
		...getDimensionStyle("padding", padding),
		backgroundAttachment: bgAttachment ? "fixed" : "",
		backgroundPosition: bgPosition,
		backgroundRepeat: bgRepeat ? "repeat" : "no-repeat",
		backgroundColor: bgColor,
		backgroundSize: bgSize,
		borderRadius: borderRadius,
	};

	const overlayStyle = {
		backgroundColor: overlay,
		opacity: opacity / 10,
	};
	const contentStyle = {
		width: getResponsiveRangeVal(contentWidth),
	};

	if (bgImage && bgImage.url) {
		wrapperStyle.backgroundImage = `url(${bgImage.url})`;
	}

	const Cta_Btn_Template = [
		[
			"core/buttons",
			{
				align: "left",
				layout: {
					type: "flex",
					justifyContent: "left",
				},
			},
			[
				[
					"core/button",
					{
						text: "Buy Now",
					},
				],
			],
		],
	];

	return (
		<>
			<BlockControls>
				<AlignmentToolbar
					value={alignment}
					onChange={(alignment) => setAttributes({ alignment })}
				/>
			</BlockControls>
			<InspectorPanel {...props} />
			<div {...blockProps} style={wrapperStyle}>
				<div className={`${prefix}-cta-overlay`} style={overlayStyle}></div>
				<div
					style={contentStyle}
					className={`${prefix}-align-${alignment} ${prefix}-cta-content`}
				>
					<div>
						<RichText
							placeholder={__("Heading...", "wc-booster")}
							value={heading}
							tagName="h2"
							onChange={(heading) => setAttributes({ heading })}
							style={textStyle}
						/>
					</div>
					<div>
						<RichText
							placeholder={__(
								"Curabitur ullamcorper ultricies...",
								"wc-booster",
							)}
							tagName="p"
							value={subDescription}
							onChange={(subDescription) => setAttributes({ subDescription })}
							style={subDescriptionStyle}
						/>
					</div>					
							
					<div>
						<InnerBlocks
							template={Cta_Btn_Template}
							allowedBlocks={["core/buttons"]}
							templateLock ={'all'}
						/>
					</div>
				</div>
			</div>
		</>
	);
};

export default Edit;
