import { PanelColor, BackgroundPosition, FixedBackgroundColor, SwitchControl } from "../../../custom-control";

import {
	InspectorControls
} from "@wordpress/block-editor";

import Dimension from "../../../custom-control/dimension";

import Upgrader from "../../../components/upgrader";
import ImageControl from "../../../custom-control/image-control";

import {  PanelBody, RangeControl, SelectControl  } from "@wordpress/components";
import { __ } from "@wordpress/i18n";

import Typography from "../../../custom-control/typography";

import metadata from "../block.json";

import { getDefaultAttr, isUndefined } from "../../../helpers";

import ResponsiveRangeWrapperControl from "../../../custom-control/responsive-range-wrapper";

const InspectorPanel = (props) => {
	const {
		attributes: {	
			bgImage,
			bgPosition,
			bgColor,
			bgAttachment,
			bgSize,	
			bgRepeat,	
			padding,
			overlay,
			opacity,
			borderRadius,
			contentWidth,	
			headingColor,
			headingTypo,
			subDescriptionColor,
			subDescriptionTypo,
			subDescriptionMargin,
			contentAlignment
		},
		setAttributes,
	} = props;

	const def = getDefaultAttr(metadata);

	return (
		<>
			<InspectorControls>
				<Upgrader 
					description="Upgrade to the pro version for additional features and enhancements."
				/>
				<PanelBody title={__("General", "wc-booster")} initialOpen={true}>
					<ResponsiveRangeWrapperControl
						label={__("CONTENT WIDTH", "wc-booster")}
						defaultValue={def("contentWidth")}
						value={contentWidth}
						onChange={(contentWidth) => setAttributes({ contentWidth })}
						beforeIcon=""
					/>
					<SelectControl
						label={__("Content Alignment", "wc-booster")}
						options={[
							{ label: __("Left", "wc-booster"), value: "left" },
							{ label: __("Right", "wc-booster"), value: "right" },
						]}
						value={contentAlignment}
						onChange={(contentAlignment) => setAttributes({ contentAlignment })}
					/>
					<RangeControl
						label="Border Radius"
						value={ borderRadius }
						onChange={(borderRadius) => {
							setAttributes({ borderRadius });
						}}
						min={ 0 }
						max={ 100 }
					/>
					<RangeControl
						label={__( "Overlay Opacity", "wc-booster")}
						value={ opacity }
						onChange={(opacity) => {
							setAttributes({ opacity });
						}}
						min={ 0 }
						max={ 10 }
					/>
				</PanelBody>

				<PanelBody title={__("Image", "wc-booster")} initialOpen={false}>
					<ImageControl
						onSelect={(bgImage) => {
							setAttributes({ bgImage });
						}}
						value={bgImage}
					/>
					<BackgroundPosition
						value={bgPosition}
						onChange={(bgPosition) => setAttributes({ bgPosition })}
					/>
					<SelectControl
						label={__("Background Size", "wc-booster")}
						options={[
							{ label: __("Cover", "wc-booster"), value: "cover" },
							{ label: __("Contain", "wc-booster"), value: "contain" },
						]}
						value={bgSize}
						onChange={(bgSize) => setAttributes({ bgSize })}
					/>
					<FixedBackgroundColor
						checked={bgAttachment}
						onChange={(bgAttachment) => setAttributes({ bgAttachment })}
					/>	
					<SwitchControl
						label={__("Background Repeat", "wc-booster")}
						checked={bgRepeat}
						onChange={(bgRepeat) => setAttributes({ bgRepeat })}
					/>	
				</PanelBody>


				<PanelBody title={__("Color", "wc-booster")} initialOpen={false}>
					<PanelColor
						colorSettings={[
							{
								label: __("Overlay Color"),
								value: overlay,
								onChange: (overlay) =>
								  setAttributes({ overlay: isUndefined( overlay ) ? '' : overlay }),
							  },
							{
								label: __("Background Color"),
								value: bgColor,
								onChange: (bgColor) => setAttributes({ bgColor: isUndefined( bgColor ) ? '' : bgColor }),
							},							
							{
								label: __("Heading Color"),
								value: headingColor,
								onChange: (headingColor) => setAttributes({ headingColor: isUndefined( headingColor ) ? '' : headingColor }),
							},
							{
								label: __("Sub Description Color"),
								value: subDescriptionColor,
								onChange: (subDescriptionColor) => setAttributes({ subDescriptionColor: isUndefined( subDescriptionColor ) ? '' : subDescriptionColor }),
							}
							
						]}
					/>
				</PanelBody>
				<PanelBody title={__("Typography", "wc-booster")} initialOpen={false}>
					<Typography
						label={__("Heading", "wc-booster")}
						value={headingTypo}
						defaultValue={metadata.attributes.headingTypo.default}
						onChange={(headingTypo) => setAttributes({ headingTypo })}
					/>
					<hr />
					<Typography
						label={__("Sub Description", "wc-booster")}
						value={subDescriptionTypo}
						defaultValue={metadata.attributes.subDescriptionTypo.default}
						onChange={(subDescriptionTypo) => setAttributes({ subDescriptionTypo })}
					/>
				</PanelBody>

				<PanelBody title={__("Spacing", "wc-booster")} initialOpen={false}>
					<Dimension
						label={__("Padding", "wc-booster")}
						defaultValue={def("padding")}
						value={padding}
						onChange={(padding) => setAttributes({ padding })}
					/>
					<Dimension
						label={__("Sub Description Margin", "wc-booster")}
						defaultValue={def("subDescriptionMargin")}
						value={subDescriptionMargin}
						onChange={(subDescriptionMargin) => setAttributes({ subDescriptionMargin })}
					/>
					
				</PanelBody>

			</InspectorControls>
		</>
	);
};

export default InspectorPanel;
